import React, { useState, useEffect, useCallback } from 'react';
import { Link } from 'gatsby';

import * as styles from '../styles/navbar.module.css';

export default function Navbar() {

  const [y, setY] = useState(0);
  const [menuActive, setMenuActive] = useState(false);

  const handleScroll = useCallback(
    (e) => {
      const window = e.currentTarget;
      setY(window.scrollY);
    }, []
  );

  const handleClick = (e) => {
    e.preventDefault();
    setMenuActive(!menuActive);
  }
  
  useEffect(() => {
    setY(window.scrollY);
    window.addEventListener("scroll", handleScroll);
  
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, [handleScroll]);;

  return(
    <div className={y <= 1 ? styles.navbar : styles.navbarElevated}>
      <div className={styles.navWrapper}>
        
        <Link to="/">
          <h4>FM</h4>
        </Link>

        <img 
          src="/menu_black_24dp.svg" 
          alt="Mobile Menu"
          className={styles.mobileMenu}
          onClick={handleClick}/>

        <nav className={menuActive ? styles.visible : ''}>
          <Link to="/">H<span>o</span>me</Link>
          <Link to="/about">About <span>M</span>e</Link>
          <Link to="/projects">Proj<span>e</span>cts</Link>

          <img 
            src="/menu_black_24dp.svg" 
            alt="Close Menu"
            className={styles.closeBtn}
            onClick={handleClick}/>
        </nav>
      </div>
    </div>
  );
}