import React from 'react';
import { Link } from 'gatsby';

export default function ProjectCard({ project={}, type="featured" }) {

  const cardStyle = `project-card ${project.color} ${type}`;
  
  return (
    <Link to={`/projects/${project.slug}`} className={cardStyle}>
      <h4>{project.title}</h4>
      <h5>{project.stack}</h5>

      <p>{project.description}</p>
    </Link>
  )
}