import React from "react";
import { graphql } from 'gatsby';

import Layout from '../components/Layout';

import FeaturedProjects from '../components/FeaturedProjects';
import AboutComponent from "../components/About";
import AchievementTracker from "../components/AchievementTracker";

import * as styles from '../styles/banner.module.css';

//TODO: About page image resizing

export default function Home({ data }) {

  const projects = data.featuredProjects.nodes;

  return (
    <Layout>
      <header className={styles.banner}>
        <h1>Fernando Mendoza</h1>
        <h3>Student + Developer</h3>
      </header>

      <section>
        <h2>My Favourite Projects</h2>
        <FeaturedProjects projects={projects}/>
      </section>

      <section>
        <h2>Who am I?</h2>
        <AboutComponent />
      </section>

      <section>
        <h2>Achievements</h2>
        <AchievementTracker />
      </section>
    </Layout>
  );
}

export const query = graphql`
  query HomePage {
    site {
      siteMetadata {
        description
        developedBy
        title
      }
    }
    featuredProjects: allMarkdownRemark(filter: {frontmatter: {featured: {eq: true}}}) {
      nodes {
        frontmatter {
          title
          stack
          description
          slug
          featured
          color
        }
      }
    }
  }
`